export default {
	data() {
		return {
			isChecked:false,
			checkboxData:[
			{'value':0,'label':'选项一'},
			{'value':1,'label':'选项二'},
			{'value':2,'label':'选项三'},
			{'value':3,'label':'选项四'},
			{'value':4,'label':'选项五'},
			{'value':5,'label':'选项六'},
			{'value':6,'label':'选项七'},
			{'value':7,'label':'选项八'},
			{'value':8,'label':'选项九'},
			{'value':9,'label':'选项十'}
		],
		checkedArr:[], //复选框选中的值
		allChecked:false //是否全选
		};
	},
	methods:{
		//布置作业
		goAssignWork(){
			uni.navigateTo({
				url:'/pages/coursesTaken/classIsOver/assignWork'
			})
		},
		changeCheckbox(e){
			this.checkedArr = e.detail.value;
			// 如果选择的数组中有值，并且长度等于列表的长度，就是全选
			if(this.checkedArr.length>0 && this.checkedArr.length==this.checkboxData.length){
				this.allChecked=true;
			}else{
				this.allChecked=false;
			}
		},
		// 全选事件
		allChoose(e){
			let chooseItem = e.detail.value;
			// 全选
			if(chooseItem[0]=='all'){
				this.allChecked=true;
				for(let item of this.checkboxData){
					let itemVal=String(item.value);
					if(!this.checkedArr.includes(itemVal)){
						this.checkedArr.push(itemVal);
					}
				}					
			}else{
				// 取消全选
				this.allChecked=false;
				this.checkedArr=[];
			}
		}
	}
}
